<template>
<section class="section main-section">
    
    <div class="container box" style="width:100%">
 
     <div v-if="webcams.length">
 <div v-if="recordingOn"> 
          <b-notification
            :type="'is-dark'"
            class="not-box"
            :closable="false">
            <div class="not-txt"> Webcam busy. Check <b>Record</b> tab <div class="dot-flashing" style="display: inline-block;margin-left: 2rem;"></div>
            <p><small>You can view live recording in the video files by enabiling streaming, not recomanded on slow hardware.</small></p>
            </div>
        </b-notification>
   </div><div v-else>
      <h2 class="subtitle"> Live Video </h2>

                   <b-button
                type="is-warning"  
                :loading="loading_start"
                @click="handleStartVideo()"
                >
                <b-icon
                icon="checkbox-blank-circle"
                size="is-small"
                type="is-success">
            </b-icon> Start
            </b-button>
                      <b-button
                type="is-warning"  
                :loading="loading_stop"
                @click="handleStopVideo()"
                style="margin-left:1rem"
                >
                <b-icon
                icon="checkbox-blank-circle"
                size="is-small"
                type="is-danger">
            </b-icon> Stop
            </b-button>
     </div></div>
     <div v-else>
           {{noWebcamsMsg}}
     </div>
    <div  v-if="liveOn" >
    <figure class="image is-5by4">
        <img ref="liveImg" :src="imgSrc" :style="imgStyle">
    </figure>
    
    <div class="box" style="margin-top:2rem;">
        <div class="field">
            <b-switch 
                @input="switchImgModeChanged"
                v-model="viewModeActualSize"
                passive-type='is-dark'
                type='is-warning'>
                {{ viewModeActualSize ? "Actual Web Cam Size Mode" : "Responsive Size Mode" }}
            </b-switch>
        </div>
        <div v-if="!viewModeActualSize"> 
        <b-field label="Image Size">
            <b-slider @change="imgSliderHasChanged" v-model="sliderValue" :min="30" :max="100"></b-slider>
        </b-field>
        </div>
        <div v-else>
          <p> Image was set cu current size of webcam : {{ defaultCamSize }} </p>
        </div>
 
          <div class="buttons">
            <b-button @click="imgTofullScreen" style="margin-left: auto;margin-right: auto;"> <b-icon
                icon="fullscreen"
                size="is-small"
                type="is-dark"
                >
            </b-icon> Scale to full screen</b-button>
        </div>
    </div>
   </div>

    </div>


  </section>
</template>


<script>

 
export default {
  name: 'ViewLive',
  
  data () {
    return {
      error:false,
    loading_start:false,
    loading_stop:false,
    errorMsg:'',
    liveOn: false,
    apiInUse:false,
    recordingOn:false,
    checkRecOnInt:undefined,
    imgSrc:this.$axios.defaults.baseURL + this.$axios.defaults.apiEndpoints['live-video-feed'] +'?ts=' + new Date().getTime() +'&t='+this.$store.state.token,
    viewModeActualSize:false,
    webcams:[],
    defaultCamSize:'',
    imgStyle:'',
    sliderValue:100
    }
  },
  props: {
    msg: String
  },
  created(){
  this.noWebcamsMsg ='No webcams detected please conect one and recheck.';


   let checkRecordOn = async () => {
     let resp = await this.$axios.get(this.$axios.defaults.apiEndpoints['record-status']);
        
        this.recordingOn = resp.data.recording;
        if( ! this.recordingOn && typeof(this.checkRecOnInt) !== undefined){
          clearInterval(this.checkRecOnInt);
        }
        return true;
    }

    checkRecordOn().then(
      () => {
         if(this.recordingOn){
           this.checkRecOnInt = setInterval(checkRecordOn, 175)
         }

      }


    );

    this.$axios.get(this.$axios.defaults.apiEndpoints['get-def-cam-size']).then(
    (res) => {
        this.defaultCamSize = res.data.defCamSize;
    })

    this.$axios.get(this.$axios.defaults.apiEndpoints['check-webcams']).then(
    (res) => {
        this.webcams = res.data;
    }

    ).catch( () => {
        this.webcams = [];
    } );


  },
  beforeDestroy(){
    
    if(typeof(this.checkRecOnInt) !== 'undefined'){
      clearInterval(this.checkRecOnInt);
    }

    if(this.liveOn){
      this.handleStopVideo()
    }

  },
  methods:{
async handleStartVideo() {
if(this.apiInUse) return;
this.loading_start = true;
this.apiInUse = true;
try {
     await this.$axios.get(this.$axios.defaults.apiEndpoints['live-video-start']);
     this.imgSrc = this.$axios.defaults.baseURL + this.$axios.defaults.apiEndpoints['live-video-feed'] +'?ts=' + new Date().getTime() +'&t='+this.$store.state.token;
     this.loading_start = false;
     this.liveOn = true;
     this.apiInUse = false;
 }catch( _error ){
this.loading_start = false;
this.apiInUse = false;
 }
 
 
},
imgSliderHasChanged(inputValue){
    this.imgStyle = `width: ${inputValue}%;height: ${inputValue}%;margin:auto;`
},
switchImgModeChanged(inputValue){
  if(inputValue){
    let rez = this.defaultCamSize.split('x');
    this.imgStyle = `width: ${Number(rez[0]).valueOf()}px;height: ${Number(rez[1]).valueOf()}px;margin:auto;`
  }else{
    this.imgStyle = `width: ${this.sliderValue}%;height: ${this.sliderValue}%;margin:auto;` 
  }

},
async handleStopVideo(){
if(this.apiInUse) return;
this.loading_stop = true;
this.apiInUse = true;
try {
     await this.$axios.get(this.$axios.defaults.apiEndpoints['live-video-stop']);
     this.loading_stop = false;
     this.liveOn = false;
     this.apiInUse = false;
 }catch( _error ){
this.loading_stop = false;
this.apiInUse = false;
 }
 
},
async imgTofullScreen(){

let elem = this.$refs.liveImg;
console.log(elem)
  if (elem.requestFullscreen) {
    elem.requestFullscreen();
  } else if (elem.mozRequestFullScreen) { /* Firefox */
    elem.mozRequestFullScreen();
  } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari & Opera */
    elem.webkitRequestFullscreen();
  } else if (elem.msRequestFullscreen) { /* IE/Edge */
    elem.msRequestFullscreen();
  }
 




}




  },
  computed: {
  onlineWebCamsFirst: function () {
     let webcams = this.webcams;
    return webcams.sort(( a, b )=> {
        if (a.active === b.active) {
         return  Number(a.camera[a.camera.length -1]).valueOf() - Number(b.camera[b.camera.length -1]).valueOf();
      }
         return b.active - a.active;
     })
  },
  onlyOnlineWebcams(){
      return this.webcams.filter((webcam)=> {
          return webcam.active
      })
  }
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
 figure {
    margin-top: 1.5rem;
 }

</style>
